@use "global" as *;

@font-face {
  font-family: "Noto Sans JP VF";
  src: url("../../fonts/NotoSansJP-VF.woff2") format("woff2");
  font-weight: 100 900;
  font-style: normal;
  font-display: swap;
}

@if $startFrom == sp {
  //スマホファースト
  html {
    //375px~767px
    font-size: 16px;

    //~375px
    @media (max-width: 375px) {
      font-size: vw(375, 16);
    }

    @include mq("md") {
      font-size: vw($inner-pc + $padding-pc * 2, 16);
    }

    //inner~max-screen
    @media (min-width: #{$inner-pc + $padding-pc * 2}px) {
      font-size: 16px;
    }
  }
} @else {
  //PCファースト
  html {
    font-size: 16px;

    @media (max-width: #{$inner-pc + $padding-pc * 2}px) {
      font-size: vw($inner-pc + $padding-pc * 2, 16);
    }

    @include mq("md") {
      font-size: 16px;
    }

    //~375px
    @media (max-width: 375px) {
      font-size: vw(375, 16);
    }
  }
}

:root {
  --base-font-family: "Noto Sans JP VF", sans-serif;

  --color-black: #111;
  --color-white: #fff;

  --gradient-green-yellow: linear-gradient(#00ae79, #b9c880);

  --header-height: #{rem(60)};
  --border-radius: #{rem(4)};
  
  @include mq() {
    --header-height: #{rem(90)};
    
    --border-radius: #{rem(8)};
  }
}

/* pcの電話番号発信対応 */
@if $startFrom == sp {
  a[href^="tel:"] {
    @include mq() {
      pointer-events: none;
    }
  }
} @else {
  a[href^="tel:"] {
    pointer-events: none;
    @include mq() {
      pointer-events: initial;
    }
  }
}

a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

/* hover指定できるPCを想定したスタイル */
@media (hover: hover) {
  a:hover {
    opacity: 0.7;
  }
}

/* hoverが使えないタッチ端末を想定した装飾 */
@media (hover: none) {
  a:active {
    opacity: 0.7;
  }
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: rem(60);
  @include mq() {
    scroll-padding-top: rem(100);
  }
}

body {
  container-type: inline-size; 
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  font-family: var(--base-font-family);
  line-height: 1.5;
  color: var(--color-black);
  overflow-wrap: anywhere; /* 収まらない場合に折り返す */
  word-break: normal; /* 単語の分割はデフォルトに依存 */
  line-break: strict; /* 禁則処理を厳格に適用 */
}

main {
  flex: 1;
}

a:where([target="_blank"]) {
  font-size: var(--font-size, #{rem(16)}); // フォールバック値付き
}

a:where([target="_blank"])::after {
  margin-left: rem(6);
  display: inline-block;
  block-size: var(--icon-size, #{rem(16)});
  aspect-ratio: 1;
  background: url(../images/common/icon_external.svg) no-repeat center / contain;
  content: "";
  transform: translateY(calc((var(--font-size, #{rem(16)}) - var(--icon-size, #{rem(16)})) / -2 + 2px) );
}

